import React, { useState } from 'react';

function FrameInputModal({ onFrameNumberSubmit, NUMBEROFFRAMES }) {
  const [frameNumber, setFrameNumber] = useState('');
  const [press, setpress] = useState(false);
  const [isOpen, setIsOpen] = useState(true);

  const [isValed, setIsValed] = useState(false);
  const [isHovered, setIsHovered] = useState(false); // لحالة hover

  const handleSubmit = e => {
    e.preventDefault();
    setpress(true);
    if (frameNumber < 1 || frameNumber > NUMBEROFFRAMES) {
      setIsValed(false);
      return;
    }

    onFrameNumberSubmit(frameNumber);
    setIsOpen(false); // اغلاق المودال بعد تقديم الرقم
  };

  if (!isOpen) {
    return null;
  }

  return (
    <div style={modalOverlayStyles}>
      <div style={modalStyles}>
        <h3 style={headerStyles}>Enter Frame Number</h3>
        <form onSubmit={handleSubmit}>
          <input
            type="number"
            value={frameNumber}
            onChange={e => setFrameNumber(e.target.value)}
            style={inputStyles}
            placeholder="Enter frame number"
          />
          <h5
            style={
              isValed
                ? { display: 'none' }
                : press
                  ? { display: 'block', color: 'red' }
                  : { display: 'none' }
            }
          >
            the number must be between 1 and {NUMBEROFFRAMES}
          </h5>
          <button
            type="submit"
            style={
              isHovered ? { ...submitButtonStyles, ...submitButtonHoverStyles } : submitButtonStyles
            } // تطبيق hover
            onMouseEnter={() => setIsHovered(true)} // عند الدخول على الزر
            onMouseLeave={() => setIsHovered(false)} // عند مغادرة الزر
          >
            Submit
          </button>
        </form>
      </div>
    </div>
  );
}

const modalOverlayStyles = {
  position: 'fixed',
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  backgroundColor: 'rgba(0, 0, 0, 0.7)', // جعل الخلفية أكثر قتامة
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  zIndex: 9999,
};

const modalStyles = {
  backgroundColor: '#050615', // اللون الداكن المتوافق مع OHIF
  borderRadius: '8px',
  padding: '20px',
  width: '320px', // زيادة العرض قليلاً لتناسب التصميم
  boxShadow: '0 4px 8px rgba(0, 0, 0, 0.3)',
  textAlign: 'center',
};

const headerStyles = {
  color: '#ffffff', // لون النص يكون أبيض ليتناسب مع الخلفية الداكنة
  fontFamily: 'Arial, sans-serif',
  fontSize: '18px',
  marginBottom: '10px',
};

const inputStyles = {
  width: '100%',
  padding: '12px',
  margin: '12px 0',
  borderRadius: '5px',
  border: '1px solid #444', // استخدام حدود داكنة بدلاً من الحدود العادية
  backgroundColor: '#153065', // خلفية غامقة مع نص فاتح
  color: '#fff', // النص بلون أبيض
  fontSize: '14px',
};

const submitButtonStyles = {
  padding: '10px 24px',
  backgroundColor: '#041c49', // اللون الأزرق المتوافق مع تصميم OHIF
  color: '#fff',
  border: 'none',
  borderRadius: '5px',
  cursor: 'pointer',
  fontSize: '16px',
  marginTop: '10px',
  transition: 'background-color 0.3s', // إضافة تأثير التحويل
};

const submitButtonHoverStyles = {
  backgroundColor: '#0c103b', // اللون الأزرق الغامق عند المرور على الزر
};

export default FrameInputModal;
